.calculator {
  width: 300px;
  border: 2px solid #ccc;
  border-radius: 5px;
  margin: auto;
  padding: 10px;
  font-size: 18px;
}

.display {
  margin-bottom: 10px;
}

.display input {
  width: 100%;
  height: 50px;
  background-color: #f2f2f2;
  border: none;
  padding: 5px;
  font-size: 24px;
}

.buttons {
  grid-template-columns: repeat(4, 1fr);
  gap: 5px;
  display: grid;
}

button {
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: #f1f1f1;
  border: none;
  border-radius: 5px;
  margin: 5px;
  font-size: 20px;
  transition: background-color .3s;
  box-shadow: 1px 1px 1px #0003;
}

button:hover {
  background-color: #ddd;
}

button:active {
  box-shadow: none;
  transform: translateY(1px);
}

button.operator {
  color: #fff;
  background-color: #f90;
  box-shadow: 1px 1px 1px #0003, inset 1px 1px 1px #ffffff80;
}

button.operator:hover {
  background-color: #e68a00;
}

.img {
  width: 10%;
  background-position: center;
  background-repeat: no-repeat;
}

